import React, { useState } from 'react';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import WatchListPopup from './WatchListPopup';
import "./WatchList.css";
import AddCircleOutlineIcon from '@mui/icons-material/AddCircleOutline';

const App = () => {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [watchlists, setWatchlists] = useState([
    { name: 'WatchList 1', expanded: false },
    { name: 'WatchList 2', expanded: false }
  ]);

  const openModal = () => setIsModalOpen(true);
  const closeModal = () => setIsModalOpen(false);

  const handleSaveWatchlist = (name) => {
    setWatchlists([...watchlists, { name, expanded: false }]);
    closeModal();
  };

  const toggleExpand = (index) => {
    setWatchlists(watchlists.map((watchlist, i) => (
      i === index ? { ...watchlist, expanded: !watchlist.expanded } : watchlist
    )));
  };

  return (
    <div className="app-container">
      <div className="flex justify-between items-center mt-6">
        <div className="text-xl font-bold">All Watchlists</div>
        <a href="/watchlist"><div className="text-green-500" style={{fontSize : "20px"}}>View all</div></a>
      </div>
      <div className="watchlist-container">
        {watchlists.map((watchlist, index) => (
          <div key={index} className="watchlist-card black-border light-black" style={{backgroundColor : "rgb(39, 37, 37)"}}>
            <div className="header watchlist-contents flex justify-between items-center light-black" onClick={() => toggleExpand(index)}>
              <h2 style={{fontSize : "20px"}}>{watchlist.name}</h2>
              <KeyboardArrowDownIcon />
            </div>
            {watchlist.expanded && (
              <div className="watchlist-content light-black">
                <p style={{fontSize : "20px",padding:"10px"}}>
                  Watchlist items
                </p>
              </div>
            )}
          </div>
        ))}
        <div className="create-watchlist-button black-border">
          <button
            onClick={openModal}
            className={`p-[1px] rounded-full ${isModalOpen ? "text-green-500" : "hover:bg-gray-500"}`}
          >
            <AddCircleOutlineIcon />
          </button>
          <span style={{paddingRight : "50px", color: "#45F6CCff" }}>Create New Watchlist</span>
        </div>
      </div>

      {isModalOpen && <div className="modal-overlay"></div>}

      <WatchListPopup
        isOpen={isModalOpen}
        onClose={closeModal}
        onSave={handleSaveWatchlist}
      />
    </div>
  );
};

export default App;
